import { useState, useEffect } from "react";
import { addCourse, updateCourse, deleteCourse } from "../api";

export default function Modal({ isOpen, onClose, onSubmit, course, refreshCourses }) {
  const [formData, setFormData] = useState(
    course || { title: "", code: "", credits: "", description: "", image: "" }
  );

  // Reset the form when the modal opens
  useEffect(() => {
    setFormData(course || { title: "", code: "", credits: "", description: "", image: "" });
  }, [course]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      if (course) {
        // Update course
        await updateCourse(course.id, formData);
      } else {
        // Add new course
        await addCourse(formData);
      }
      refreshCourses(); // Refresh the course list after the operation
      onClose(); // Close the modal
    } catch (error) {
      console.error("Error submitting the course:", error);
      alert("Failed to submit the course. Please try again.");
    }
  };

  const handleDelete = async () => {
    if (!course) return; // Only delete if a course is selected
    try {
      await deleteCourse(course.id);
      refreshCourses(); // Refresh the course list after deletion
      onClose(); // Close the modal
    } catch (error) {
      console.error("Error deleting the course:", error);
      alert("Failed to delete the course. Please try again.");
    }
  };

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center">
      <div className="bg-white p-6 rounded-md w-96">
        <h2 className="text-xl font-bold mb-4">
          {course ? `Edit Course: ${course.title}` : "Add New Course"}
        </h2>
        <form onSubmit={handleSubmit}>
          <input
            type="text"
            name="title"
            placeholder="Course Name"
            value={formData.title}
            onChange={handleChange}
            className="w-full p-2 mb-2 border rounded"
            required
          />
          <input
            type="text"
            name="code"
            placeholder="Course Code"
            value={formData.code}
            onChange={handleChange}
            className="w-full p-2 mb-2 border rounded"
            required
          />
          <input
            type="number"
            name="credits"
            placeholder="Credits"
            value={formData.credits}
            onChange={handleChange}
            className="w-full p-2 mb-2 border rounded"
            required
          />
          <textarea
            name="description"
            placeholder="Description"
            value={formData.description}
            onChange={handleChange}
            className="w-full p-2 mb-2 border rounded"
            required
          />
          <input
            type="text"
            name="image"
            placeholder="Image URL"
            value={formData.image}
            onChange={handleChange}
            className="w-full p-2 mb-4 border rounded"
            required
          />
          <div className="flex justify-end gap-2">
            <button
              type="button"
              onClick={onClose}
              className="px-4 py-2 bg-gray-200 text-gray-700 rounded"
            >
              Cancel
            </button>
            <button
              type="submit"
              className="px-4 py-2 bg-blue-500 text-white rounded"
            >
              {course ? "Update" : "Submit"}
            </button>
            {course && (
              <button
                type="button"
                onClick={handleDelete}
                className="px-4 py-2 bg-red-500 text-white rounded-md shadow hover:bg-red-600"
              >
                Delete
              </button>
            )}
          </div>
        </form>
      </div>
    </div>
  );
}
